.header {
  height: 13.76rem;
  background: url('../../../assets/header_background.png');
  background-size: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2.666667rem;
  .myStyle {
    display: flex;
    flex-direction: column;
    align-items: center;
    .ant-progress-circle {
      .ant-progress-text {
        color: #63fe9a;
        font-size: 1.92rem;
      }
    }
  }

  .CoreText {
    margin-top: -1.333333rem;
    color: #ffffff;
    font-size: 0.373333rem;
    font-family: 'PingFangSC-Regular,PingFang SC';
  }
  .buttonStyle {
    width: 8.933333rem;
    height: 1.333333rem;
    background-color: #fff;
    margin-bottom: 0.64rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #3c8af7;
    border-radius: 8px;
    font-family: 'PingFangSC-Regular,PingFang SC';
    .picStyle {
      width: 0.64rem;
      margin-right: 0.533333rem;
    }
  }
  .topButton {
    margin-top: 1.6rem;
  }
}
