.login {
  height: 100%;
  background-color: #ffffff;
  padding: 0 0.533333rem;
  h1 {
    font-size: 32px;
    font-weight: 500;
    color: rgba(30, 30, 30, 1);
    font-family: 'NotoSansCJKsc-Medium,NotoSansCJKsc';
  }
  .login_delete {
    height: 1.173333rem;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    img {
      width: 0.533333rem;
      height: 0.586667rem;
    }
  }
  .login_header {
    span {
      color: #919fb1;
      font-size: 13px;
      font-family: 'NotoSansCJKsc-Regular, NotoSansCJKsc';
    }
  }
  .login_content {
    span {
      color: #919fb1;
      font-size: 13px;
      font-family: 'NotoSansCJKsc-Regular, NotoSansCJKsc';
    }
  }

  .login_btn {
    display: flex;
    justify-content: center;
    button {
      margin: 0;
      padding: 0;
      border: 1px solid transparent;
      outline: none;
      width: 5.786667rem;
      height: 1.28rem;
      background-color: #2d7bf8;
      color: #ffffff;
      font-size: 17px;
      border-radius: 8px;
      margin-top: 1.066667rem;
    }
  }
  input {
    border: none;
    width: 100%;
    height: 1.066667rem;
    margin-top: 0.533333rem;
    box-shadow: inset 0px -2px 1px -1px #c8c7cc;
    &::placeholder {
      color: #c4c4c4;
    }
  }
  .get_code {
    display: flex;
    span {
      margin-top: 0.533333rem;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3.2rem;
      font-size: 16px;
      color: #2d7bf8;

      box-shadow: inset 0px -2px 1px -1px #c8c7cc;
    }
  }
}
