.App {
  .am-navbar {
    border-bottom: 0.5px solid #f6f6f6;
    width: 10rem;
  }
  .nav_bar {
    box-shadow: inset 0px -1px 1px -1px #c8c7cc;
    width: 100%;
  }
}
.bodyContent {
  height: calc(100vh - 45px);
  background-color: #ffffff;
  .contentPart {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 1.493333rem;
    margin: 0 0.426667rem;
    border-bottom: 0.5px solid #eeeeee;
    font-size: 17px;
    font-family: 'PingFangSC-Regular,PingFang SC';
    font-weight: 520;
    color: #1e1e1e;
    img {
      width: 24px;
      margin-right: 0.32rem;
    }
  }
}

.grid {
  margin: 0.48rem auto;
  width: 9.146667rem;
  height: 4.4rem;
  border-radius: 8px;
  background-color: #ffffff;
  display: flex;
  flex-wrap: wrap;
  div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 50%;
    height: 50%;
    box-shadow: inset 1px -1px 1px -1px #c8c7cc;
    padding: 0.373333rem 0.533333rem;
    .top {
      color: #919fb1;
    }
    .bottom {
      color: #1e1e1e;
      font-weight: bold;
    }
  }
}

.describe {
  padding: 0.426667rem;
  color: #1e1e1e;
}

.health_content {
  height: 100vh;
  background-color: #ffffff;

  .trend {
    width: 10rem;
    height: 8rem;
  }
  .health_index {
    width: 10rem;
    height: 8rem;
  }
}
