.personal_header {
  background-image: url('../../assets/personal_main.png');
  background-size: contain;
  width: 10rem;
  height: 8.906667rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img {
    width: 2.133333rem;
  }
  span {
    margin-top: 0.32rem;
    font-size: 20px;
    font-family: 'DINPro-Medium,DINPro';
    color: #ffffff;
  }
}

.person_options {
  width: auto;
  margin: -2.32rem 0.533333rem 0 0.533333rem;
  height: 5.173333rem;
  background: rgba(255, 255, 255, 1);
  border-radius: 8px;
  box-shadow: 0px 3px 12px 0px rgba(0, 0, 0, 0.03);
  display: flex;
  flex-direction: column;
  a {
    flex: 1;
    box-shadow: inset 0px -1px 1px -1px #c8c7cc;
    .bind_message,
    .feedback,
    .quit {
      flex: 1;

      padding: 0.426667rem;
      display: flex;
      justify-content: space-between;
      align-items: center;
      img {
        width: 0.64rem;
        height: 0.64rem;
        margin-right: 0.426667rem;
      }
      & > div {
        display: flex;
        justify-content: flex-start;
      }
    }
  }

  .textword {
    font-size: 16px;
    color: #1e1e1e;
    font-family: 'PingFangSC-Regular,PingFang SC';
    font-weight: 400;
  }
}

.bindMessage {
  height: 100vh;
  background-color: #ffffff;
  .nav_bar {
    box-shadow: inset 0px -1px 1px -1px #c8c7cc;
  }
  .no_bind {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding-top: 1.866667rem;
    img {
      width: 1.6rem;
      margin-bottom: 0.64rem;
    }
    span {
      font-size: 17px;
      font-family: 'PingFangSC-Regular,PingFang SC';
      color: #919fb1;
      font-weight: 400;
    }
  }
  .has_bind {
    padding: 0 0.533333rem;
    height: 3rem;
    display: flex;
    align-items: center;
    .bind_left {
      img {
        width: 1.706667rem;
        margin-right: 0.533333rem;
      }
    }
    .bind_right {
      display: flex;
      flex-direction: column;
      .name {
        font-size: 0.453333rem;
        font-weight: 550;
        font-family: 'PingFangSC-Medium,PingFang SC';
        color: #1e1e1e;
      }
      .bind_class,
      .number {
        font-size: 14px;
        font-family: 'PingFangSC-Regular, PingFang SC';
        font-weight: 400;
        color: #1e1e1e;
      }
    }
  }
}

.feed_back {
  height: 100vh;
  background-color: #ffffff;
  .nav_bar {
    box-shadow: inset 0px -1px 1px -1px #c8c7cc;
  }
  .feed_content {
    padding: 0 0.426667rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    .text_area {
      width: 100%;
      height: 4.213333rem;
      border-radius: 8px;
      border: none;
      background-color: #f7f7f7;
      padding: 0.32rem;
      margin-top: 0.426667rem;
      margin-bottom: 1.12rem;
      color: #c4c4c4;
      &::-webkit-input-placeholder {
        color: #c4c4c4;
      }
    }
    .btn {
      width: 5.786667rem;
      height: 1.28rem;
      background-color: #2d7bf8;
      color: #ffffff;
      font-size: 17px;
      font-family: 'PingFangSC-Regular,PingFang SC';
      font-weight: 400;
      margin: 0;
      padding: 0;
      border: 1px solid transparent; //自定义边框
      outline: none;
      border-radius: 8px;
    }
  }
}
